.form {
  display: block;
  padding-top: 45px;
  padding-bottom: 50px;
  padding-left: 50px;
  padding-right: 50px;
  background-color: $c_main;

  @include mobile {
    padding-top: 35px;
    padding-bottom: 35px;
    padding-left: 30px;
    padding-right: 30px;
  }
}

.form p {
  margin-bottom: 25px;

  @include font(18px, 1.5, $med);
  color: $c_light-one;

  @include tablet {
    text-align: left;
  }
}

.form input[type="text"],
.form input[type="tel"],
.form input[type="email"] {
  width: 100%;
  margin-bottom: 17px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 15px;
  border: 3px solid transparent;
  outline: 0 none;

  @include font(14px, 1.5, $reg);
  color: $c_main;

  &::placeholder {
    @include font(14px, 1.5, $reg);
    color: $c_main;
  }

  &:hover {
    border: 3px solid rgba($c_main, 0.5);
  }

  &:focus,
  &:active {
    border: 3px solid rgba($c_main, 0.5);
  }

  @include tablet {
    padding-top: 9px;
    padding-bottom: 9px;
  }

  @include mobile {
    margin-bottom: 20px;
    padding-top: 8px;
    padding-bottom: 8px;
  }
}

.form input[type="checkbox"] {
  @include visually-hidden;
}

.form input[type="checkbox"] + label {
  position: relative;
  display: block;
  padding-left: 22px;

  @include font(10px, 1.3, $reg);
  color: $c_light-one;

  @include mobile {
    @include font(10px, 1.2, $reg);
    padding-left: 30px;
  }
}

.form input[type="checkbox"] + label::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  letter-spacing: -0.2px;
  background-color: $c_light-one;
}

.form input[type="checkbox"] + label::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  background-repeat: no-repeat;
  background-position: 53% 50%;
  background-size: 14px 10px;
  background-image: url("../img/icon-tick.svg");
  opacity: 0.2;
}

.form input[type="checkbox"]:checked + label {
  &::after {
    opacity: 1;
  }
}

.form input[type="checkbox"]:disabled + label {
  &::before {
    opacity: 0.5;
  }

  &::after {
    opacity: 0.1;
  }
}

.form input[type="checkbox"]:disabled:checked + label {
  &::before {
    opacity: 0.6;
  }

  &::after {
    opacity: 1;
  }
}

.form__btn {
  width: 100%;
  margin-top: 25px;
}
